body {
  margin: 0;
}

.warning-icon {
  color: #FFA726;
}

.check-icon {
  color: #8BC34A;
}

#login {
  text-align: center;
}
#login .title {
  padding: 15px 15px;
}
#login #customBtn {
  display: inline-block;
  background: white;
  color: #444;
  width: 190px;
  border-radius: 5px;
  border: thin solid #888;
  -webkit-box-shadow: 1px 1px 1px grey;
          box-shadow: 1px 1px 1px grey;
  white-space: nowrap;
}
#login #customBtn:hover {
  cursor: pointer;
}
#login span.label {
  font-family: serif;
  font-weight: normal;
}
#login span.icon {
  background: url("/g-normal.png") transparent 5px 50% no-repeat;
  display: inline-block;
  vertical-align: middle;
  width: 42px;
  height: 42px;
}
#login span.buttonText {
  display: inline-block;
  vertical-align: middle;
  padding-left: 42px;
  padding-right: 42px;
  font-size: 14px;
  font-weight: bold;
  /* Use the Roboto font that is loaded in the <head> */
  font-family: "Roboto", sans-serif;
}

#admin-users {
  text-align: center;
}
#admin-users #new-email {
  text-align: center;
  margin: 15px 0;
}

#loader-container {
  position: absolute;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(15, 15, 150, 0.5);
}
#loader-container #center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
#loader-container #center #image {
  margin-top: 150px;
  -webkit-animation: ld-bounce 2s infinite;
          animation: ld-bounce 2s infinite;
}
@-webkit-keyframes ld-bounce {
  0%, 90% {
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
  }
  10% {
    -webkit-animation-timing-function: cubic-bezier(0, 0.4, 0.6, 1);
            animation-timing-function: cubic-bezier(0, 0.4, 0.6, 1);
    -webkit-transform: translate(0%, 5%) scaleY(1.1);
    transform: translate(0%, 5%) scaleY(1.1);
  }
  50% {
    -webkit-animation-timing-function: cubic-bezier(0.4, 0, 1, 0.6);
            animation-timing-function: cubic-bezier(0.4, 0, 1, 0.6);
    -webkit-transform: translate(0%, -37%) scaleY(1.1);
    transform: translate(0%, -37%) scaleY(1.1);
  }
  0%, 100% {
    -webkit-transform: translate(0%, 30%) scaleY(0.5);
    transform: translate(0%, 30%) scaleY(0.5);
  }
  90% {
    -webkit-transform: translate(0%, 5%) scaleY(1.1);
    transform: translate(0%, 5%) scaleY(1.1);
  }
}
@keyframes ld-bounce {
  0%, 90% {
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
  }
  10% {
    -webkit-animation-timing-function: cubic-bezier(0, 0.4, 0.6, 1);
            animation-timing-function: cubic-bezier(0, 0.4, 0.6, 1);
    -webkit-transform: translate(0%, 5%) scaleY(1.1);
    transform: translate(0%, 5%) scaleY(1.1);
  }
  50% {
    -webkit-animation-timing-function: cubic-bezier(0.4, 0, 1, 0.6);
            animation-timing-function: cubic-bezier(0.4, 0, 1, 0.6);
    -webkit-transform: translate(0%, -37%) scaleY(1.1);
    transform: translate(0%, -37%) scaleY(1.1);
  }
  0%, 100% {
    -webkit-transform: translate(0%, 30%) scaleY(0.5);
    transform: translate(0%, 30%) scaleY(0.5);
  }
  90% {
    -webkit-transform: translate(0%, 5%) scaleY(1.1);
    transform: translate(0%, 5%) scaleY(1.1);
  }
}

.mount-animation-container {
  position: absolute;
  width: 100%;
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0.4;
    left: 15px;
  }
  to {
    opacity: 1;
    left: 0;
  }
}

@keyframes fade-in {
  from {
    opacity: 0.4;
    left: 15px;
  }
  to {
    opacity: 1;
    left: 0;
  }
}
#summary #success {
  position: absolute;
  top: 0;
  width: 100%;
}
#summary #success #green-box {
  padding-top: 20px;
  background-color: #8dd48c;
  min-width: 100%;
  min-height: 180px;
  color: white;
}
#summary #success .big-check-icon {
  width: 80px;
  height: 80px;
}
#summary #order-details-container {
  margin: 45px 45px;
}
#summary #order-details-container .right-side-detail {
  text-align: right;
}
#summary #order-details-container .centered-detail {
  text-align: center;
}

#landing {
  text-align: center;
}
#landing #background {
  position: absolute;
  width: 100%;
  height: 100vh;
  background: url("../images/si-logo.jpg");
  background-position-y: center;
  opacity: 0.1;
  -webkit-animation: right-to-left 15s linear infinite;
  animation: right-to-left 15s linear infinite;
}
@-webkit-keyframes right-to-left {
  100% {
    background-position-x: 291px;
  }
}
@keyframes right-to-left {
