  width: 50px;
}

.product-dialog-image {
  max-width: 100%;
}

#new-product .input-container {
  text-align: center;
  margin: 15px 0;
}

#product-list .thumbnail {
  max-width: 50px;
  max-height: 50px;
}

#statistics .statistics-container {
  padding: 25px 0;
}

.single-field-container {
  text-align: center;
  padding: 15px 0;
}

.button-icon {
  margin-right: 10px;
}

.product-dialog {
  text-align: center;
}

.product-summary-expansion-panel-detail {
  padding: 8px 24px 24px;
}



